import React, {Component} from 'react'
import PropTypes from 'prop-types'
import { withStyles } from '@material-ui/core/styles'
import withWidth from '@material-ui/core/withWidth'
import AppBar from '@material-ui/core/AppBar'
import Toolbar from '@material-ui/core/Toolbar'
import Typography from '@material-ui/core/Typography'
import IconButton from '@material-ui/core/IconButton'
import Hidden from '@material-ui/core/Hidden'

import FilterListIcon from '@material-ui/icons/FilterList'

import viewOnGithub from '../assets/img/view-on-github.black.png'

const styles = theme => ({
  root: {
    zIndex: theme.zIndex.drawer + 1
  },
  viewOnGithub: {
    position: 'fixed',
    top: 0,
    right: 0,
    height: 110,
    zIndex: 1
  }
})

class AppHeaderContent extends Component {
  render () {
    const { classes, mobileOpen, toggleMobileDrawer, width } = this.props

    return (
      <AppBar position='absolute' className={classes.root}>
        <Toolbar disableGutters={width === 'xs' && !mobileOpen}>
          <Hidden mdUp={width !== 'xs'}>
            <IconButton
              color='inherit'
              aria-label='open drawer'
              onClick={toggleMobileDrawer}>
              <FilterListIcon />
            </IconButton>
          </Hidden>
          <Typography variant='title' color='inherit' noWrap>
            CH02 filter
          </Typography>
          <a href='https://github.com/sky172839465/the-f2e-ch02-filter' target='block'>
            <img
              className={classes.viewOnGithub}
              src={viewOnGithub}
              alt='View on github' />
          </a>
        </Toolbar>
      </AppBar>
    )
  }
}

AppHeaderContent.propTypes = {
  classes: PropTypes.object.isRequired
}

export default withStyles(styles)(withWidth()(AppHeaderContent))



