import React from 'react'
import { withStyles } from '@material-ui/core/styles'
import classNames from 'classnames'
import List from '@material-ui/core/List'
import ListItem from '@material-ui/core/ListItem'
import ListItemText from '@material-ui/core/ListItemText'
import ListSubheader from '@material-ui/core/ListSubheader'
import FormControlLabel from '@material-ui/core/FormControlLabel'
import Checkbox from '@material-ui/core/Checkbox'

// import CheckBoxOutlineBlankIcon from '@material-ui/icons/CheckBoxOutlineBlank'
// import CheckboxIcon from '@material-ui/icons/Checkbox'

const styles = theme => ({
  root: {
    position: 'relative'
  },
  AppList: {
    width: '100%',
    backgroundColor: theme.palette.background.paper,
    position: 'relative',
    overflow: 'auto',
    maxHeight: '100%'
  },
  listSection: {
    backgroundColor: 'inherit'
  },
  ul: {
    backgroundColor: 'inherit',
    padding: 0
  },
  subheader: {
    backgroundColor: '#eee'
  },
  firstLetter: {
    textTransform: 'capitalize'
  }
})

const SelectOptionsArea = (props) => {
  const {classes, optionList, selectOptions, search} = props

  return (
    <List dense className={classes.AppList}>
      {optionList.map(optionType => (
        <li key={optionType.name} className={classes.listSection}>
          <ul className={classes.ul}>
            <ListSubheader className={classNames(classes.firstLetter, classes.subheader)}>
              {optionType.name}
            </ListSubheader>
            {optionType.options.map((option, index) => (
              <div key={option}>
                <ListItem
                  button
                  dense
                  divider={index !== (optionType.options.length - 1)}
                  onClick={() => search(option, optionType.name)}>
                  <FormControlLabel
                    control={
                      <Checkbox
                        color='primary'
                        checked={selectOptions[optionType.name].indexOf(option) !== -1}
                        tabIndex={-1}
                        disableRipple />
                    } />
                  <ListItemText
                    primary={optionType.name === 'stage' ? `Stage ${option}` : option} />
                </ListItem>
              </div>
            ))}
          </ul>
        </li>
      ))}
    </List>
  )
}

export default withStyles(styles)(SelectOptionsArea)



