import * as actionTypes from './actionTypes'

const API_SERVICE = 'https://www.thef2e.com/api'

export const getTagList = () => {
  return (dispatch) => {
    fetch(`${API_SERVICE}/tagList`)
      .then(res => res.json())
      .then(data => {
        dispatch({
          type: actionTypes.GET_TAG_LIST,
          payload: {
            tagList: data
          }
        })
      })
  }
}

export const getCodeList = (selectOptions) => {
  const queryList = Object.keys(selectOptions).reduce((tempQueryList, optionType) => {
    if (selectOptions[optionType].length === 1) {
      tempQueryList.push(`${optionType}=${selectOptions[optionType].join(',')}`)
    }
    return tempQueryList
  }, [])

  return (dispatch) => {
    fetch(`${API_SERVICE}/codeList?${queryList.join('&')}`)
      .then(res => res.json())
      .then(data => {
        dispatch({
          type: actionTypes.GET_CODE_LIST,
          payload: {
            codeList: data
          }
        })
      })
  }
}



