import React from 'react'
import PropTypes from 'prop-types'
import { withStyles } from '@material-ui/core/styles'
import Card from '@material-ui/core/Card'
import CardContent from '@material-ui/core/CardContent'
import Chip from '@material-ui/core/Chip'
import List from '@material-ui/core/List'
import ListItem from '@material-ui/core/ListItem'
import ListItemIcon from '@material-ui/core/ListItemIcon'
import ListItemText from '@material-ui/core/ListItemText'

import CodeIcon from '@material-ui/icons/Code'
import CloudUploadIcon from '@material-ui/icons/CloudUpload'
import StyleIcon from '@material-ui/icons/Style'
import LinkIcon from '@material-ui/icons/Link'

import formatDistance from 'date-fns/formatDistance'

const styles = theme => ({
  card: {
    margin: '0.5rem'
  },
  bullet: {
    display: 'inline-block',
    margin: '0 2px',
    transform: 'scale(0.8)'
  },
  title: {
    marginBottom: 16,
    fontSize: 14
  },
  pos: {
    marginBottom: 12
  },
  chip: {
    margin: theme.spacing.unit / 2
  }
})

const AppCard = (props) => {
  const { classes, code } = props

  return (
    <Card className={classes.card}>
      <CardContent>
        <List component='nav'>
          <ListItem dense>
            <ListItemIcon>
              <CodeIcon />
            </ListItemIcon>
            <ListItemText primary={`Stage ${code.stage}`} />
          </ListItem>
          <ListItem dense>
            <ListItemIcon>
              <CloudUploadIcon />
            </ListItemIcon>
            <ListItemText
              primary={`Upload ${formatDistance(code.timeStamp, new Date(), {
                includeSeconds: true
              })}`} />
          </ListItem>
          <ListItem dense divider>
            <ListItemIcon>
              <StyleIcon />
            </ListItemIcon>
            <ListItemText>
              {code.tag.split(', ').map((item, index) => (
                <Chip
                  key={index}
                  label={item}
                  className={classes.chip} />
              ))}
            </ListItemText>
          </ListItem>
          <ListItem button dense component='a' target='block' href={code.url}>
            <ListItemIcon>
              <LinkIcon />
            </ListItemIcon>
            <ListItemText primary='Demo Link' />
          </ListItem>
        </List>
      </CardContent>
    </Card>
  )
}

AppCard.propTypes = {
  classes: PropTypes.object.isRequired
}

export default withStyles(styles)(AppCard)



