import React, {Component} from 'react'
import PropTypes from 'prop-types'
import * as CONSTANT from '../constant'
import { withStyles } from '@material-ui/core/styles'
import Grid from '@material-ui/core/Grid'
import Fade from '@material-ui/core/Fade'
import Paper from '@material-ui/core/Paper'
import Typography from '@material-ui/core/Typography'
import CircularProgress from '@material-ui/core/CircularProgress'
import TablePagination from '@material-ui/core/TablePagination'

import AppCard from './common/AppCard'

const styles = theme => ({
  root: {
    backgroundColor: theme.palette.background.default,
    height: '100vh',
    overflow: 'auto',
    width: '100vw',
    [theme.breakpoints.up('md')]: {
      width: `calc(100vw - ${CONSTANT.APP_BAR_WIDTH}px)`
    }
  },
  toolbar: theme.mixins.toolbar,
  paper: {
    backgroundColor: 'rgba(0, 0, 0, 0)'
  },
  message: {
    height: '80vh'
  },
  spinner: {
    textAlign: 'center'
  }
})

class AppMainContent extends Component {
  updateUI = () => {
    const { classes, codeList, selectOptions, isDataLoaded, pagination } = this.props
    const { rowsPerPage, page } = pagination
    const isOptionSelected = Object.keys(selectOptions).some(optionType => {
      return selectOptions[optionType].length > 0
    })

    if (isDataLoaded) {
      if (isOptionSelected && (codeList.length > 0)) {
        return (
          <div>
            {codeList.slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage).map((code, index) => (
              <Fade
                key={code.timeStamp}
                in
                {...({ timeout: (100 * index + 500) })}>
                <Paper elevation={0} className={classes.paper}>
                  <AppCard code={code} />
                </Paper>
              </Fade>
            ))}
          </div>
        )
      } else {
        return (
          <Typography align='center' variant='display1'>No data found !</Typography>
        )
      }
    } else {
      return (
        <div className={classes.spinner}>
          <CircularProgress size={80} />
        </div>
      )
    }
  }

  handleChangePage = (event, page) => {
    const {updatePagination} = this.props

    document.location.href = '#top'
    updatePagination({page})
  }

  handleChangeRowsPerPage = event => {
    const {updatePagination} = this.props
    const rowsPerPage = event.target.value

    updatePagination({rowsPerPage})
  }

  render () {
    const { classes, codeList, isDataLoaded, pagination } = this.props

    return (
      <main className={classes.root}>
        <div id='top' className={classes.toolbar} />
        <Grid
          container
          spacing={0}
          direction='column'
          justify='center'
          alignItems='center'
          className={((codeList.length > 0) && isDataLoaded) ? '' : classes.message}>
          <Grid item xs={12} sm={7}>
            {this.updateUI()}
          </Grid>
          {codeList.length === 0 ? '' : (
            <Grid item>
              <TablePagination
                component='div'
                backIconButtonProps={{
                  'aria-label': 'Previous Page'
                }}
                nextIconButtonProps={{
                  'aria-label': 'Next Page'
                }}
                onChangePage={this.handleChangePage}
                onChangeRowsPerPage={this.handleChangeRowsPerPage}
                {...pagination} />
            </Grid>
          )}
        </Grid>
      </main>
    )
  }
}

AppMainContent.propTypes = {
  classes: PropTypes.object.isRequired
}

export default withStyles(styles)(AppMainContent)



