import React, {Component} from 'react'
import PropTypes from 'prop-types'
import * as CONSTANT from '../constant'
import { withStyles } from '@material-ui/core/styles'
import Drawer from '@material-ui/core/Drawer'
import Hidden from '@material-ui/core/Hidden'
import SelectOptionsArea from './common/SelectOptionsArea'

const styles = theme => ({
  root: {
    position: 'relative'
  },
  toolbar: theme.mixins.toolbar,
  drawerPaper: {
    width: CONSTANT.APP_BAR_WIDTH,
    [theme.breakpoints.up('md')]: {
      position: 'relative'
    }
  },
  AppList: {
    width: '100%',
    backgroundColor: theme.palette.background.paper,
    position: 'relative',
    overflow: 'auto',
    maxHeight: '100%'
  }
})

class AppBarContent extends Component {
  constructor () {
    super()
    this.state = {
      mobileOpen: false,
      checked: {
        stage: [],
        tag: []
      }
    }
  }

  componentDidMount () {
    const {getTagList} = this.props
    getTagList()
  }

  handleDrawerToggle = () => {
    this.setState({ mobileOpen: !this.state.mobileOpen })
  }

  search = (option, type) => {
    const {updateSelectOption, setDataLoaded} = this.props
    setDataLoaded(false)
    Promise.resolve(updateSelectOption(option, type))
      .then(() => {
        const {getCodeList, selectOptions} = this.props
        getCodeList(selectOptions)
      })
  }

  render () {
    const {mobileOpen, selectOptions, classes, theme, optionList, toggleMobileDrawer} = this.props

    return (
      <div>
        <Hidden mdUp>
          <Drawer
            variant='temporary'
            anchor={theme.direction === 'rtl' ? 'right' : 'left'}
            open={mobileOpen}
            onClose={toggleMobileDrawer}
            elevation={4}
            classes={{
              paper: classes.drawerPaper
            }}
            ModalProps={{
              keepMounted: true // Better open performance on mobile.
            }}>
            <SelectOptionsArea
              optionList={optionList}
              selectOptions={selectOptions}
              search={this.search} />
          </Drawer>
        </Hidden>
        <Hidden smDown implementation='css'>
          <Drawer
            variant='permanent'
            open
            elevation={4}
            classes={{
              paper: classes.drawerPaper
            }}>
            <div className={classes.toolbar} />
            <SelectOptionsArea
              optionList={optionList}
              selectOptions={selectOptions}
              search={this.search} />
          </Drawer>
        </Hidden>
      </div>
    )
  }
}

AppBarContent.propTypes = {
  classes: PropTypes.object.isRequired
}

export default withStyles(styles, { withTheme: true })(AppBarContent)



