import * as actionTypes from './actionTypes'

export const updateSelectOption = (selectOption, optionType) => {
  return {
    type: actionTypes.UPDATE_SELECT_OPTION,
    payload: {
      selectOption,
      optionType
    }
  }
}

export const toggleMobileDrawer = () => {
  return {
    type: actionTypes.TOGGLE_MOBILE_DRAWER
  }
}

export const setDataLoaded = (isDataLoaded) => {
  return {
    type: actionTypes.SET_DATA_LOADED,
    payload: {
      isDataLoaded
    }
  }
}

export const updatePagination = (pagination) => {
  return {
    type: actionTypes.UPDATE_PAGINATION,
    payload: {
      pagination
    }
  }
}



