import * as type from './actions/actionTypes'
import {isValidURL} from './util'

const initialState = {
  mobileOpen: false,
  isDataLoaded: true,
  optionList: [],
  codeList: [],
  drawerWidth: 320,
  selectOptions: {
    stage: [],
    tag: []
  },
  pagination: {
    rowsPerPage: 10,
    page: 0,
    count: 0
  }
}

const reducer = (state = initialState, action) => {
  switch (action.type) {
    case type.GET_TAG_LIST:
      const {tagList} = action.payload
      const stageList = [...Array(9).keys()].map(x => (x += 1))

      return {
        ...state,
        optionList: [
          {name: 'stage', options: stageList},
          {name: 'tag', options: tagList}
        ]
      }
    case type.GET_CODE_LIST:
      const {codeList} = action.payload
      const {selectOptions} = state
      const optionTypeList = Object.keys(selectOptions)
      const isMultipleOptionsSelect = optionTypeList.some(optionType => selectOptions[optionType].length > 1)
      let validUrlCodeList = codeList.filter(code => isValidURL(code.url))

      if (isMultipleOptionsSelect) {
        validUrlCodeList = validUrlCodeList.filter(code => {
          return optionTypeList.every(optionType => {
            return selectOptions[optionType].every(option => {
              console.log('here', option, code[optionType])
              return `${code[optionType]}`.indexOf(`${option}`) !== -1
            })
          })
        })
        console.log(validUrlCodeList)
      }

      return {
        ...state,
        isDataLoaded: true,
        codeList: [...validUrlCodeList],
        pagination: {
          ...state.pagination,
          page: 0,
          count: validUrlCodeList.length
        }
      }
    case type.UPDATE_SELECT_OPTION:
      const {selectOption, optionType} = action.payload
      const currentIndex = state.selectOptions[optionType].indexOf(selectOption)
      const newSelectOptions = [...state.selectOptions[optionType]]

      if (currentIndex === -1) {
        newSelectOptions.push(selectOption)
      } else {
        newSelectOptions.splice(currentIndex, 1)
      }
      // const newSelectOptions = [selectOption]

      return {
        ...state,
        codeList: [],
        selectOptions: {
          ...state.selectOptions,
          [optionType]: [...newSelectOptions]
        }
      }
    case type.TOGGLE_MOBILE_DRAWER:
      return {
        ...state,
        mobileOpen: !state.mobileOpen
      }
    case type.SET_DATA_LOADED:
      return {
        ...state,
        isDataLoaded: action.payload.isDataLoaded
      }
    case type.UPDATE_PAGINATION:
      return {
        ...state,
        pagination: {
          ...state.pagination,
          ...action.payload.pagination
        }
      }
    default:
      return state
  }
}

export default reducer



