import React, { Component } from 'react'
import { connect } from 'react-redux'
import * as actionCreators from '../store/actions'

import AppBarContent from '../component/AppBarContent'

class AppBar extends Component {
  constructor () {
    super()
    this.state = {}
  }

  render () {
    return (
      <main>
        <AppBarContent {...this.props} />
      </main>
    )
  }
}

const mapStateToProps = state => {
  return {
    mobileOpen: state.mobileOpen,
    selectOptions: state.selectOptions,
    optionList: state.optionList
  }
}

const mapDispatchToProps = dispatch => {
  return {
    getTagList: () => dispatch(actionCreators.getTagList()),
    getCodeList: (selectOptions) => dispatch(actionCreators.getCodeList(selectOptions)),
    updateSelectOption: (selectOption, optionType) => dispatch(actionCreators.updateSelectOption(selectOption, optionType)),
    toggleMobileDrawer: () => dispatch(actionCreators.toggleMobileDrawer()),
    setDataLoaded: (isDataLoaded) => dispatch(actionCreators.setDataLoaded(isDataLoaded))
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(AppBar)



